import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'

@Component({
  selector: 'app-custom-allot',
  templateUrl: './custom-allot.component.html',
  styleUrls: ['./custom-allot.component.scss'],
  providers: [Commfunc],
})
export class CustomAllotComponent implements OnInit {
  show:number;
  ngOnChanges() {
  }

  constructor(
    public commfunc: Commfunc,
    public router: Router,
  ) {
  }

  ngOnInit() {
    let stu_1 = this.commfunc.permissionCheck('SID01005_P014');
    let stu_2 = this.commfunc.permissionCheck('SID01005_P015');
  
    if (stu_1) {
      this.show = 1;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-allot/my-allot'])
    } else if (stu_2) {
      this.show = 2;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-allot/mine-allot'])
    }
  }
  //点击切换
  allot(num) {
    this.show = num;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
}
}
