import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { CustomAllotDialogBean } from "app/pages/tzb/custom/custom-marketing/custom-allot/custom-allot-dialog/bean/custom-allot-dialog.bean";

@Component({
  selector: 'app-custom-allot-dialog',
  templateUrl: './custom-allot-dialog.component.html',
  styleUrls: ['./custom-allot-dialog.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class CustomAllotDialogComponent implements OnInit {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValues: any;

  msgs: Message[] = [];
  customAllotDialogBean: CustomAllotDialogBean = new CustomAllotDialogBean();
  headerTitle: string;
  display: boolean;
  peopleList: any;
  user: any;
  user1: any;
  userName: string = '';
  orgName: string = '';
  userId: string = '';
  orgId: string = '';
  receiverName: string = '';
  dates: any = [];

  constructor(
    public commfunc: Commfunc,
    public router: Router,
    public httpService: CusOperationHttpService,
  ) {
    this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user1.userId) {
      this.userId = this.user1.userId;
    }
    if (this.user1.orgId) {
      this.orgId = this.user1.orgId;
    }
    if (this.user.userName) {
      this.userName = this.user.userName;
    }
    if (this.user.orgName) {
      this.orgName = this.user.orgName;
    }
    this.customAllotDialogBean.creater = this.userId;
    this.customAllotDialogBean.createrName = this.userName;
    this.customAllotDialogBean.createOrgId = this.orgId;
    this.customAllotDialogBean.createOrgName = this.orgName;
    this.customAllotDialogBean.receiveOrgName = this.customAllotDialogBean.createOrgName;
    this.customAllotDialogBean.receiveOrgId = this.customAllotDialogBean.createOrgId;
    this.receiverName = '';
    this.customAllotDialogBean.receiverName = '';
    this.customAllotDialogBean.receiver = '';
    this.queryPeopleById()
  }

  ngOnInit() {
   
  }

  //确定
  confirmBtn() {

    if (this.inValues.type == '1') {
      this.customAllotDialogBean.operationType = '1';//分配
    }
    if (this.inValues.type == '2') {
      this.customAllotDialogBean.operationType = '2';//分配
    }
    if (this.inValues.type == '3') {
      this.customAllotDialogBean.operationType = '3';//分配
    }
    this.dates = []
    if (this.inValues.invalue[0]) {
      this.dates = this.inValues.invalue;
    } else {
      this.dates.push(this.inValues.invalue);
    }
    this.customAllotDialogBean.custList = this.dates;
    for (let i of this.peopleList) {
      if (i.value == this.receiverName) {
        this.customAllotDialogBean.receiverName = i.label;
        this.customAllotDialogBean.receiver = this.receiverName;
        continue
      }
    }
    this.httpService.insertCustManageData(this.customAllotDialogBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        let a9 = data2 => { this.outValue.emit(false) }
        setTimeout(a9, 1000);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //取消
  cancelBtn() {
    this.outValue.emit(false);
  }

  //机构树
  orgTree() {
    this.headerTitle = '机构树';
    this.display = true;
  }

  //机构树回调
  treeCall(param) {
    this.display = false;
    this.customAllotDialogBean.receiveOrgName = param.orgName;
    this.customAllotDialogBean.receiveOrgId = param.orgId;
    this.queryPeopleById()
  }

  //接收联系人
  queryPeopleById() {
    let param = {
      orgId: this.customAllotDialogBean.receiveOrgId,
      pageSize: 1000000,
      tellerStatus: "0"
    }
    this.httpService.queryTellerByCombConditions(param).subscribe(data => {
      if (data.tellerList) {
        this.peopleList = this.commfunc.listToLabelvalue(data.tellerList, 'tellerName', 'tellerId');
        this.receiverName = this.peopleList[0].value;
      }
    })
  }
}
