import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { UpdatePlanBean } from "app/pages/tzb/custom/custom-marketing/custom-contact/contact-plan/update-plan/bean/update-plan.bean";
import { Message } from 'primeng/components/common/api';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";

@Component({
  selector: 'app-add-contact',
  templateUrl: './add-contact.component.html',
  styleUrls: ['./add-contact.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class AddContactComponent implements OnInit {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';

  updatePlanBean: UpdatePlanBean = new UpdatePlanBean();
  lxzt: any = [];
  contactWay: any = [];
  contactOrg: any = [];
  contactDate: any;
  CALENDAR_CN: any = [];
  msgs: Message[] = [];
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.lxzt = this.code['Lxzt'];
    this.contactWay = this.code['ContactWays'];
    this.contactOrg = this.code['ContactOrg'];

    this.CALENDAR_CN = {
      "firstDayOfWeek": 0,
      "dayNames": ["周日", "周一", "周二", "周三", "周四", "周五", "周六"],
      "dayNamesShort": ["星期天", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"],
      "dayNamesMin": ["日", "一", "二", "三", "四", "五", "六"],
      "monthNames": ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"],
      "monthNamesShort": ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"]
    }
  }

  ngOnInit() {
  }
  ngOnChanges() {
    this.updatePlanBean = this.inValue;
    this.contactDate = new Date(this.updatePlanBean.contactDate);
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //提交-保存
  submit() {
    let ob = this.httpService.updateCustContactProgram(this.updatePlanBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //关闭
  close() {
    this.outValue.emit(false);
  }

  //日期转换
  showData() {
    this.updatePlanBean.contactDate = this.commfunc.transDateN(this.contactDate);
  }
}
