import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomAllotComponent } from './custom-allot.component';//客户分配
import { MyAllotComponent } from './my-allot/my-allot.component';//我要分配
import { MineAllotComponent } from './mine-allot/mine-allot.component';//我的分配
 /* 客户分配
 * 
 *
 */
export const routes: Routes = [

    {
        path: '',
        component: CustomAllotComponent,
        children: [
            { path: '', redirectTo: 'my-allot', pathMatch: 'full' },
            {
                path: 'my-allot', component: MyAllotComponent,
            },
            {
                path: 'mine-allot', component: MineAllotComponent,
            },
        ]
    },

]

export const CusAllotRouting: ModuleWithProviders = RouterModule.forChild(routes);
